body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-overflow-style: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.p_paragraph {
    font-size: 75px;
    margin-top: 2vh;
    margin-bottom: 5vh;
    color: brown;
    text-align: center;
    text-shadow: 8px 8px 8px #3d3d3d;
}
.container-one {
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    /* Animation */
    margin: 0 auto;
    width: 70%;
    text-align: center;
  
}

.container-one:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    width: 60%;    
    /* zoom on hover */
    
}

.container-one:hover .hidden_img {
    display: block;
    width: auto;
}
.container-one .hidden_img {
    position:absolute;
    top:50%;
    left:50%;
    display:none;
    cursor: zoom-in;
    border: 0px;
    background: transparent;
}


*, *::before, *::after {
    box-sizing: border-box;
}
.paragraph {
    font-size: 1.5em;
    margin-top: 4%;
    margin-bottom: 4%;
    color: #000;
    text-align: center;
    text-shadow: 8px 8px 8px #3d3d3d;
}
.carousel {
    width: 100%;
    height: 40vh;
    position: relative;
}

.carousel>ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.slide {
    position: absolute;
    inset: 0;
    opacity: 0;
    transition: 150ms opacity ease-in-out;
    transition-delay: 150ms;
}

.slide>img {
    display: block;
    margin: 5% auto;
    width: 100%;
    height: 35vh;
    object-fit: cover;
    object-position: center;
}

.slide[data-active] {
    opacity: 1;
    z-index: 1;
    transition-delay: 0ms;
}

.carousel-button {
    position: absolute;
    z-index: 2;
    background: none;
    border: none;
    font-size: 4rem;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    color: rgba(255, 255, 255, .5);
    cursor: pointer;
    border-radius: .25rem;
    padding: 0 .5rem;
    background-color: rgba(0, 0, 0, .1);
}

.carousel-button:hover,
.carousel-button:focus {
    color: white;
    background-color: rgba(0, 0, 0, .2);
}

.carousel-button:focus {
    outline: 1px solid black;
}

.carousel-button.prev {
    left: 1rem;
}

.carousel-button.next {
    right: 1rem;
}
h1 {
  text-align: center;
  margin: 50px 0;
}

.box {
  width: 70%;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.2);
  padding: 5%;
  border: 2px solid #fff;
  border-radius: 20px/50px;
  background-clip: padding-box;
  text-align: center;
}

.button {
  font-size: 1em;
  padding: 10px;
  color: #fff;
  border: 2px solid #06D85F;
  border-radius: 20px;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.button:hover {
  background: #06D85F;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  transition: opacity 300ms;
  /* visibility: hidden; */
  /* opacity: 1; */
}

.overlay:target {
  visibility: visible;
  opacity: 1;
}

.popup {
  height: 70vh;
  margin: 70px auto;
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  width: 70vw;
  position: relative;
  transition: all 2s ease-in-out;
}

.popup h2 {
  margin-top: 3%;
  margin-bottom: 0;
  color: #333;
  font-family: Tahoma, Arial, sans-serif;
}

.popup .close {
  position: absolute;
  top: 20px;
  right: 30px;
  transition: all 200ms;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  color: #333;
}

.popup .close:hover {
  color: brown;
}

.container {
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}
